<template>
    <!--<vue-draggable-resizable-->
            <!--:active="false"-->
            <!--:resizable="true"-->
            <!--:parent="true"-->
            <!--:handles="[]"-->
            <!--:w="imageWidth"-->
            <!--:h="imageHeight"-->
            <!--:x="image.x"-->
            <!--:y="image.y">-->

        <div class="picture"
             :class="[!loaded? 'picture--loading' : '']"
             :style="{
                left: 'calc(50% - ' + (this.passepartoutWidthAdaptive * coefficient) + 'px - ' + (this.frameWidthAdaptive * coefficient) + 'px - (' + this.imageWidth * this.coefficient + 'px / 2))',
                top: 'calc(30% - ' + (this.passepartoutWidthAdaptive * coefficient) + 'px - ' + (this.frameWidthAdaptive * coefficient) + 'px - (' + this.imageHeight * this.coefficient + 'px / 2))',
              }">
            <div class="picture__frame"
                 :style="[(frameActive) ? {
                    borderWidth: frameWidthAdaptive * coefficient + 'px',
                    borderImageSource: 'url(' + framePath + ')',
                    borderImageSlice: 'fill ' + frameSlice,
                 } : '']">
                <div class="picture__passepartout"
                     :style="{
                        borderWidth: passepartoutWidthAdaptive * coefficient + 'px',
                        borderColor: passepartoutColor
                    }">

                    <div class="picture__image"
                         :style="{
                            width: imageWidth * coefficient + 'px',
                            height: imageHeight * coefficient + 'px',
                            backgroundImage: 'url(' + imagePath + ')',
                        }">
                    </div>

                </div>
            </div>
        </div>
    <!--</vue-draggable-resizable>-->
</template>

<script>
    import VueDraggableResizable from 'vue-draggable-resizable'
    import Axios from 'axios'

    export default {
        name: "Picture",
        components: {
            VueDraggableResizable
        },
        data() {
            return {
                loaded: false,
                image: {
                    path: null,
                    x: 0,
                    y: 0,
                },
            }
        },
        computed: {
            coefficient() {
                return this.$store.state.background.coefficient;
            },
            frameActive() {
                return this.$store.state.frame.active
            },
            framePath() {
                return this.$store.state.frame.path
            },
            frameSlice() {
                return this.$store.state.frame.slice
            },
            frameWidthAdaptive() {
                return this.$store.state.frame.widthAdaptive
            },
            passepartoutColor() {
                return this.$store.state.passepartout.color
            },
            passepartoutWidthAdaptive() {
                return this.$store.state.passepartout.widthAdaptive
            },
            imagePath() {
                return this.$store.state.image.path
            },
            imageWidth() {
                return this.$store.state.image.width
            },
            imageHeight() {
                return this.$store.state.image.height
            }
        },
        watch: {
            imagePath(val) {
                var downloadingImage = new Image();

                downloadingImage.src = val;
                downloadingImage.onload = () => {
                    this.image.path = val;
                    this.loaded = true;
                };
                console.log('loaded');
            }
        },
        methods: {

            updatePicture(path, width, height) {
                this.$store.commit('updateImagePath', path);
                this.$store.commit('updateImageWidth', width);
                this.$store.commit('updateImageHeight', height);
            },
        },
        beforeCreate() {
            Axios.get(this.$api + 'site/get-picture-by-id?id=' + this.$route.query.id)
                .then((res) => {
                    this.updatePicture(this.$api + res.data.picture, res.data.w, res.data.h);
                }, () => {
                    this.$store.state.error = true;
                })
        },
        created() {
            this.$store.commit('updateImageWidth', 80);
            this.$store.commit('updateImageHeight', 120);
        },
        mounted() {
            this.image.x =  'calc(50% - ' + this.passepartoutWidthAdaptive + 'px - ' + this.frameWidthAdaptive + 'px - (' + this.imageWidth * this.coefficient + 'px / 2))';
            this.image.y = 'calc(30% - ' + this.passepartoutWidthAdaptive + 'px - ' + this.frameWidthAdaptive + 'px - (' + this.imageHeight * this.coefficient + 'px / 2))';
        },
        updated() {
            setTimeout(() => {
                document.getElementsByClassName('picture')[0].style.transition = 'left .3s, top .3s';
            }, 300);
        }

    }
</script>

<style lang="scss">
    .picture {
        position: absolute;
        display: inline-block;
        box-shadow: -4px 0px 8px #000000;
        border-radius: 3px;
        &--loading {
            box-shadow: none;
            &::before {
                border-radius: inherit;
                content: '';
                background: #fefcfa;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                /*z-index: 100;*/
            }
            &::after {
                content: '';
                display: block;
                width: 3.111111111rem;
                height: 3.111111111rem;
                background-color: #000;
                border-radius: 50%;
                animation: pulse 1s linear infinite;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                /*z-index: 110;*/
            }
        }
        &__frame {
            transition: border .3s;
            border-style: solid;
            border-color: rgba(0, 0, 0, 0.4);
            border-width: 0;
            //border-image-source: url("../../assets/img/frame-1.png");
            border-image-slice: fill 100;
        }
        &__passepartout {
            transition: border .3s;
            border: 30px solid white;
        }
        &__image {
            box-sizing: content-box;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            &.dragging {
                cursor: move;
            }
        }
    }
</style>