import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


const store = () => new Vuex.Store({

    state: {
        error: false,

        background: {
            path: require('@/assets/img/interrior-3.jpg'),
            coefficient: 410,
        },
        frame: {
            active: false,
            path: require('../src/assets/img/frame-1.png'),
            type: 1,
            width: 0,
            widthAdaptive: 0,
            slice: 100
        },
        passepartout : {
            width: 0,
            widthAdaptive: 0,
            color: '#FFFFFF',
        },
        image: {
            path: '',
            width: 0,
            height: 0,
        },

    },
    mutations: {
        updateInterior(state, interior) {
            this.state.background.path = interior.path;
            this.state.background.coefficient = interior.coefficient;
        },
        updateFrameWidth(state, width) {
            this.state.frame.width = width;
            this.state.frame.widthAdaptive = (width / 100);
            if (this.state.frame.active === false) {
                this.state.frame.active = true;
                this.state.frame.type = 1
            }
        },
        updateFrameType(state, type) {
            if (type === null) {
                this.state.frame.active = false;
            } else {
                this.state.frame.active = true;
                this.state.frame.type = type.id;
                this.state.frame.path = require('../src/assets/img/frame-' + type.id + '.png');
                this.state.frame.slice = type.slice;

                if (this.state.frame.width === 0) {
                    this.commit('updateFrameWidth', 5.5);
                }

            }

        },
        updatePassepartoutColor(state, color) {
            this.state.passepartout.color = color;
            if (this.state.passepartout.width === 0 && this.state.passepartout.color !== '#FFFFFF') {
                this.commit('updatePassepartoutWidth', 10);
            }
        },
        updatePassepartoutWidth(state, width) {
            this.state.passepartout.width = width;
            this.state.passepartout.widthAdaptive = (width / 100);
        },
        updateImagePath(state, path) {
            this.state.image.path = path;
        },
        updateImageWidth(state, width) {
            this.state.image.width = (width / 100);
        },
        updateImageHeight(state, width) {
            this.state.image.height = (width / 100);
        },
    }
});

export default store